import * as Linking from 'expo-linking';

const makeUrl = (path: string) => `${process.env.PUBLIC_URL}/${path}`;

export default {
  prefixes: [Linking.makeUrl(process.env.PUBLIC_URL)],
  config: {
    screens: {
      Root: makeUrl('/'),
      Playground: makeUrl('/play'),
      Ranking: makeUrl('/ranking'),
      NotFound: '*',
    },
  },
};

